import Dexie, { Table } from 'dexie';

export interface DBStockRecord {
  id?: number,
  documentId: number,
  name: string,
  price: number,
  position: number,
}

export interface DBPasswordRecord {
  id?: number,
  documentId: number,
  description: string,
  name: string,
  password: string
}

export enum DBDocumentTypeEnum {
  "stock",
  "password",
}

export interface DBDocumentStoreItem {
  id?: number,
  name: string,
  type: DBDocumentTypeEnum,
}

