import Drawer from "components/Drawer/Drawer";
import StockList from "components/StockList/StockList";
import Password from "components/Password/Password";
import { useIndexSelector, useIndexDispatch } from "components/index/index_hooks";
import { setInteractingDocumentId, } from "components/App/App_slice";
import * as React from "react";
import Box from '@mui/material/Box';
import { useLiveQuery } from "dexie-react-hooks";
import { db, DBDocumentTypeEnum } from "database/db";

const App = () => {
  // ____ _    ____ ___  ____ _       ____ ___ ____ ___ ____
  // | __ |    |  | |__] |__| |       [__   |  |__|  |  |___
  // |__] |___ |__| |__] |  | |___    ___]  |  |  |  |  |___
  const dispatch = useIndexDispatch();
  const { interactingDocumentId } = useIndexSelector((state) => {
    return {
      interactingDocumentId: state.App.App.interactingDocumentId,
    }
  });

  // _    ____ ____ ____ _       ____ ___ ____ ___ ____
  // |    |  | |    |__| |       [__   |  |__|  |  |___
  // |___ |__| |___ |  | |___    ___]  |  |  |  |  |___

  // _  _ ____ ____    _  _ ____ ____ _  _ ____
  // |  | [__  |___    |__| |  | |  | |_/  [__
  // |__| ___] |___    |  | |__| |__| | \_ ___]
  const documentArray = useLiveQuery(
    () => db.documentStore.toArray(),
    []
  )

  const interactingDocument = useLiveQuery(
    () => db.documentStore.get(interactingDocumentId),
    [interactingDocumentId]
  )

  React.useEffect(() => {
    (async () => {
      if (interactingDocumentId < 0 && documentArray !== undefined && documentArray.length) {
        dispatch(setInteractingDocumentId(documentArray[0].id as number));
      }
    })();
  }, [documentArray, interactingDocumentId]);

  // ____ _  _ _  _ ____ ___ _ ____ _  _ ____
  // |___ |  | |\ | |     |  | |  | |\ | [__
  // |    |__| | \| |___  |  | |__| | \| ___]
  const PageContent = React.useCallback(() => {
    let Component;

    if (interactingDocumentId < 0) {
      Component = <Box
        display="flex"
        justifyContent="center"
      >
        <h1
          style={{
            userSelect: "none"
          }}
        >
          No Document have been made
        </h1>
      </Box>
    } else if (interactingDocument === undefined) {
      Component = <Box
        display="flex"
        justifyContent="center"
      >
        <h1
          style={{
            userSelect: "none"
          }}
        >
          Loading . . .
        </h1>
      </Box>
    } else if (interactingDocument.type === DBDocumentTypeEnum.stock) {
      Component = <StockList />
    } else if (interactingDocument.type === DBDocumentTypeEnum.password) {
      Component = <Password />
    } else {
      Component = <></>
    }

    return Component;
  }, [interactingDocumentId, interactingDocument]);

  // ____ ____ ___ _  _ ____ _  _
  // |__/ |___  |  |  | |__/ |\ |
  // |  \ |___  |  |__| |  \ | \|
  if (documentArray === undefined ||
    interactingDocument === undefined) {
    return <></>
  } else {

    return (<>
      <Drawer />
      <PageContent />
    </>);
  }
}

export default App